import { useState } from 'react';
import Input from '../../components/Form/Input';
import Label from '../../components/Form/Label';
import Select from '../../components/Form/Select';
import ActionButton from '../../components/ui/Buttons/ActionButton';
import Arrow from '../../components/ui/Icons/Arrow/Arrow';
import { t } from '../../helpers/i18n/dictionary';
import useConfig from '../../hooks/config/useConfig';
import useDomain from '../../hooks/domain/useDomain';

type ProvisionState = 'Idle' | 'Loading' | 'Success' | 'Error' | 'Failed';

const Provision = () => {
  const [provisionState, setProvisionState] = useState<ProvisionState>('Idle');
  const [domain, setDomain] = useState<{ prefix: string; domain: string }>({
    prefix: 'tao.nahs',
    domain: '.truvian.id',
  });
  const { data: config } = useConfig().fetch;
  const {
    reserve: {
      mutateAsync: reserveDomain,
      isLoading: reserveLoading,
      reset: resetReserver,
      isIdle: isReserveIdle,
    },
    register: { mutateAsync: registerDomain, status: registerStatus, reset: resetRegister },
  } = useDomain();
  const [reservationId, setReservationId] = useState<string | undefined>(undefined);
  const [registrationId, setRegistrationId] = useState<string | undefined>(undefined);

  const doReserveDomain = async (fullDomain: string) => {
    // Should be debounced
    setProvisionState('Loading');

    // Mutate
    try {
      const reservation = await reserveDomain({ domain: fullDomain });

      if (!reservation) {
        // => Domain not available
        setProvisionState('Idle');
        return;
      }
      setReservationId(reservation);

      setProvisionState('Idle');
    } catch (ex) {
      setProvisionState('Failed');
      return;
    }
  };

  const doRegister = async () => {
    setProvisionState('Loading');
    if (!reservationId) {
      //If no id, reservation might not have happened yet
      await doReserveDomain(domain.prefix + domain.domain);

      if (!reservationId) {
        // => Domain not available
        setProvisionState('Idle');
        return;
      }
    }

    try {
      const registration = await registerDomain({
        domain: domain.prefix + domain.domain,
        reservationId: reservationId,
      });

      if (!registration) {
        // => Registartion Failed
        setProvisionState('Error');
        return;
      }

      setRegistrationId(registration);
      setProvisionState('Success');
    } catch (ex) {
      setProvisionState('Failed');
      return;
    }
  };

  return (
    <section className="flex flex-grow flex-col">
      <div className="container mx-auto flex h-full min-h-full flex-grow flex-col px-5">
        <div className="mt-20 min-h-[20rem]">
          <h1 className="mb-10 text-4xl">
            Younivere | Signup
            <span className="mt-1 block text-3xl text-slate-400">Create a new identity</span>
          </h1>
          {provisionState === 'Idle' || provisionState === 'Loading' ? (
            <form
              onSubmit={(e) => {
                e.preventDefault();
                doRegister();
                return false;
              }}
              className={`${provisionState === 'Loading' ? 'pointer-events-none' : ''}`}
            >
              <div className="-m-2 flex flex-row flex-wrap items-center">
                <div className="w-1/2 p-2 md:w-2/5">
                  <Label htmlFor="prefix">Domain Prefix</Label>
                  <Input
                    name="prefix"
                    type="text"
                    defaultValue={domain.prefix}
                    debouncedOnChange={(e) => {
                      const newDomain = { ...domain, prefix: e.target.value };
                      setDomain(newDomain);
                      doReserveDomain(newDomain.prefix + newDomain.domain);
                    }}
                  />
                </div>
                <div className="w-1/2 p-2 md:w-2/5">
                  <Label>Domain</Label>
                  <Select
                    onChange={(e) => {
                      const newDomain = { ...domain, prefix: e.target.value };
                      setDomain(newDomain);
                      doReserveDomain(newDomain.prefix + newDomain.domain);
                    }}
                    className="h-[2.66rem]"
                  >
                    {config?.managedDomains.map((domain) => (
                      <option key={domain} value={domain}>
                        {domain}
                      </option>
                    ))}
                  </Select>
                </div>
                <div className="mt-auto flex-shrink-0 flex-grow p-2">
                  <ActionButton
                    state={registerStatus}
                    className="h-[2.66rem] w-full"
                    isDisabled={!(domain.domain && domain.prefix && reservationId)}
                    icon="send"
                  >
                    {t('Register your identity')}
                  </ActionButton>
                </div>
              </div>
              <div className="mt-2">
                <p>
                  {domain.domain &&
                  domain.prefix &&
                  !reservationId &&
                  !reserveLoading &&
                  !isReserveIdle ? (
                    <>{t('This identity is no longer available, try again')}</>
                  ) : null}
                </p>
              </div>
            </form>
          ) : provisionState === 'Success' ? (
            <div className="text-center">
              <h3>Your new identity is ready to use!</h3>
              <div className="p-2"> ᕦ(ò_óˇ)ᕤ</div>
              <h2>Your new identity: {domain.prefix + domain.domain}</h2>
              <a href={`http://${domain.prefix + domain.domain}/firstrun?frt=${registrationId}`}>
                click here to continue to {domain.prefix + domain.domain}
              </a>
            </div>
          ) : provisionState === 'Failed' ? (
            <div>
              <p className="text-lg">
                {t('The registration failed, please get in touch...')}{' '}
                <a href="/contact" className="underline underline-offset-2">
                  {t('Contact options')} <Arrow className="ml-1 inline h-4 w-4" />
                </a>
              </p>
              <p className="mt-8">{t('Want to try again?')}</p>

              <ActionButton
                onClick={() => {
                  setProvisionState('Idle');
                  setReservationId(undefined);
                  setRegistrationId(undefined);
                  resetReserver();
                  resetRegister();
                }}
                type="secondary"
                className="mt-3"
                icon="times"
              >
                {t('Start again')}
              </ActionButton>
            </div>
          ) : (
            <div>
              <p className="text-lg">
                {t('Mmh something went wrong, and we are not sure what happened exactly...')}{' '}
                {t('Want to try again?')}
              </p>
              <ActionButton
                onClick={() => {
                  setProvisionState('Idle');
                  setReservationId(undefined);
                  setRegistrationId(undefined);
                  resetReserver();
                  resetRegister();
                }}
                type="secondary"
                className="mt-3"
                icon="times"
              >
                {t('Start again')}
              </ActionButton>
            </div>
          )}
        </div>
      </div>
    </section>
  );
};

export default Provision;
