import { useMutation } from '@tanstack/react-query';
import axios from 'axios';

const useDomain = () => {
  const root = '//' + window.location.host + '/api/v1';

  const reserveDomain = async ({ domain }: { domain: string }) => {
    let isAvailableUrl = root + '/registration/availability/' + domain;
    let isAvailable = await axios.get<boolean>(isAvailableUrl).then((response) => response.data);

    if (!isAvailable) {
      return false;
    }

    let url = root + '/registration/reservations';
    let data = {
      domainName: domain,
      //"previousReservationId": ""  //must be a valid guid or not included in the payload
    };

    const reserveResponse = await axios
      .post<{ id: string }>(url, data)
      .then((response) => response.data);

    return reserveResponse.id;
  };

  const registerDomain = async ({
    domain,
    reservationId,
  }: {
    domain: string;
    reservationId: string;
  }) => {
    let url = root + '/registration';
    let data = {
      domainName: domain,
      registrationInfo: {
        reservationId: reservationId,
      },
    };

    const response = await axios.post<string>(url, data).then((response) => response.data);

    return response;
  };

  return {
    reserve: useMutation(reserveDomain, {
      onError: (ex) => {
        console.error(ex);
      },
    }),
    register: useMutation(registerDomain, {
      onError: (ex) => {
        console.error(ex);
      },
    }),
  };
};

export default useDomain;
